import React, {useState} from 'react'
import Navbar from '../components/Navbar'
import Sidebar from '../components/Sidebar'
import HeroSection from '../components/HeroSection'
// import LocationSection from '../components/LocationSection'
import LearningSection from '../components/LearningSection'
import ShopSection from '../components/ShopSection'
import AboutSection from '../components/AboutSection'
import { homeObjOne } from '../components/InfoSection/Data'
import { homeObjTwo } from '../components/LearningSection/Datalearning'
import { homeObjThree } from '../components/ShopSection/Datashop'
import { homeObjFour } from '../components/AboutSection/DataAbout'
import Services from '../components/Services'
import Footer from '../components/Footer'

const Home = () => {
    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => {
        setIsOpen(!isOpen);
    }
    return (
        <>
            <Sidebar isOpen={isOpen} toggle={toggle} />
            <Navbar toggle={toggle} />
            <HeroSection />
            <Services />
            {/* <LocationSection {...homeObjOne} /> */}
            <LearningSection {...homeObjTwo} />
            <ShopSection {...homeObjThree} />
            <AboutSection {...homeObjFour} />
            <Footer />
        </>
    )
}

export default Home