import React from 'react'
import { AboutContainer, AboutH1, AboutWrapper, AboutCard, AboutH2, AboutP, AboutIcon, FormIcon1, BackIcon, AboutH3, AboutW} from './AboutbElements'
import useFetch from '../useFetch'
// import { Image } from 'react-bootstrap'

const AboutB = () => {
  const apiUrl= "http://localhost:1337"
    const {loading, error,data} = useFetch(
      `${apiUrl}/api/abouts?populate=*`
    )
    if (loading) return <p>Loading...</p>
    if (error) return <p>Error...</p>
  
  return ( 
    <AboutContainer id="aboutb">
        <FormIcon1 to='/'>
            <BackIcon />
        </FormIcon1>
        <AboutH1> Tentang Kami </AboutH1>
        <AboutW> Project ISTERN merupakan wujud nyata kami untuk selalu berada disamping kamu serta menjadi teman yang selalu melindungi kamu dari bencana. Berisikan video pembelajaran bencana serta starterpack yang berguna untuk kamu suatu saat nanti. Kami menjanjikan keakuratan serta data yang valid didalam aplikasi ISTERN.  </AboutW>
        <AboutWrapper>
            {data.data.map((about) => (
              <AboutCard key={about.id}>
                <AboutIcon Image src={`${apiUrl}${about.attributes.picture.data.attributes.url}`} alt={about.attributes.title} className="favImage" />
                <AboutH2> {about.attributes.name} </AboutH2>
                <AboutH3> {about.attributes.role} </AboutH3>
                <AboutP> {about.attributes.description} </AboutP>
            </AboutCard>))}            
        </AboutWrapper>
    </AboutContainer>
  )
}

export default AboutB