import React, {useEffect, useState} from 'react'
import {FaBars} from 'react-icons/fa'
import { IconContext } from 'react-icons/lib'
import {Nav, NavbarContainer, NavLogo, MobileIcon, NavMenu, NavItem, NavLinks, NavBtn, NavBtnLink} from './NavbarElements'
import { animateScroll as scroll } from 'react-scroll'

const Navbar = ({ toggle }) => {
  const [scrollNav, setScrollNav] = useState(false)

  const changeNav = () => {
    if(window.scrollY >= 80) {
        setScrollNav(true)
    } else {
        setScrollNav(false)
    }
  }

  useEffect (() => {
    window.addEventListener('scroll', changeNav)
  }, [])

  const toggleHome = () => {
    scroll.scrollToTop();
  }

  return (
    <>
    <IconContext.Provider value={{ color: '#fff'}}>
        <Nav scrollNav={scrollNav}>
            <NavbarContainer>
                <NavLogo to='/' onClick={toggleHome}>ISTERN</NavLogo>
                <MobileIcon onClick={toggle}>
                    <FaBars />
                </MobileIcon>
                <NavMenu>
                    <NavItem>
                        {/* <NavLinks 
                            to='lokasi' 
                            smooth={true} 
                            duration={500} 
                            spy={true} 
                            exact='true' 
                            offset={-80} 
                        >Lokasi</NavLinks> */}
                    </NavItem>
                    <NavItem>
                        <NavLinks 
                            to='simulasi' 
                            smooth={true} 
                            duration={500} 
                            spy={true} 
                            exact='true' 
                            offset={-80} 
                        >Simulasi</NavLinks>
                    </NavItem>
                    <NavItem>
                        <NavLinks 
                            to='staterpack' 
                            smooth={true} 
                            duration={500} 
                            spy={true} 
                            exact='true' 
                            offset={-80}  
                        >Starterpack</NavLinks>
                    </NavItem>
                    <NavItem>
                        <NavLinks 
                            to='about' 
                            smooth={true} 
                            duration={500} 
                            spy={true} 
                            exact='true' 
                            offset={-80} 
                            >About</NavLinks>
                    </NavItem>
                </NavMenu>
            </NavbarContainer>
        </Nav> 
    </IconContext.Provider>
    </>
  )
}

export default Navbar