import React from 'react'
import { Container, FormWrap, Icon, FormContent, Form, FormH1, FormLabel, FormInput, FormButton, NavBtnLink, BackIcon, FormIcon1} from './SigninElements'

const SignIn = () => {
  return (
    <>
        <Container id='signin'>
            <FormWrap>
                <Icon to="/">ISTERN</Icon>
                <FormContent>
                    <Form action="#">
                        <FormIcon1 to='/'>
                            <BackIcon />
                        </FormIcon1>
                        <FormH1>Sign in to your account</FormH1>
                        <FormLabel htmlfor='for'>Email</FormLabel>
                        <FormInput type='email' required />
                        <FormLabel htmlfor='for'>Password</FormLabel>
                        <FormInput type='password' required />
                        <FormButton type='submit'>Continue</FormButton>
                        <NavBtnLink to='/forgetpw'>Forget your password</NavBtnLink>
                    </Form>
                </FormContent> 
            </FormWrap>
        </Container>
    </>
  )
}

export default SignIn