import React from 'react';
import './App.css';
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom';
import Home from './pages';
import SigninPage from './pages/signin';
import ForgetpwPage from './pages/forgetpw';
import LearningbPage from './pages/learningb';
import ShopbPage from './pages/shopb';
import AboutbPage from './pages/aboutb';
import LearningAPI from './components/Config/LearningAPI';
import { useEffect, useState } from 'react';
// import Search from './Search';

function App() {
  const [bests, setBests] = useState([]);

  useEffect(() => {
    LearningAPI.find().then((res) => {
      console.log(res.data);
      setBests(res.data);
    });
  }, []);

  return (
    <Router>
      <Routes>
        <Route path="/" element={<Home />} exact/>
        <Route path="/signin" element={<SigninPage />} exact/>
        <Route path="/forgetpw" element={<ForgetpwPage />} exact/>
        <Route path="/learningb" element={<LearningbPage />} exact/>
        <Route path="/shopb" element={<ShopbPage />} exact/>
        <Route path="/aboutb" element={<AboutbPage />} exact/>
        {/* <Route path="/search" element={<SearchPage />} exact/> */}
      </Routes>
    </Router>
  );
}

export default App
