import React from 'react'
import Icon1 from '../../images/support.svg'
import Icon2 from '../../images/jogging.svg'
import Icon3 from '../../images/nature.svg'
import { ServicesContainer, ServicesH1, ServicesWrapper, ServicesCard, ServicesIcon, ServicesH2, ServicesP} from './ServicesElements'

const Services = () => {
  return (
    <ServicesContainer id="services">
        <ServicesH1> Our Services </ServicesH1>
        <ServicesWrapper>
            <ServicesCard>
                <ServicesIcon src={Icon1} />
                <ServicesH2>Komunitas Bencana</ServicesH2>
                <ServicesP>Bergabung bersama ribuan user lainnya dengan puluhan cerita didalamnya</ServicesP>
            </ServicesCard>
            <ServicesCard>
                <ServicesIcon src={Icon2} />
                <ServicesH2>Virtual Learning</ServicesH2>
                <ServicesP>Akses video pembelajaran dimanapun dan kapanpun anda berada</ServicesP>
            </ServicesCard>
            <ServicesCard>
                <ServicesIcon src={Icon3} />
                <ServicesH2>Premium Items</ServicesH2>
                <ServicesP>Rasakan manfaat dan kemewahan dari item bencana kami</ServicesP>
            </ServicesCard>
        </ServicesWrapper>
    </ServicesContainer>
  )
}

export default Services