import React from 'react'
import { LearningContainer, LearningH1, LearningWrapper, LearningCard, LearningH2, LearningP, LearningIcon, FormIcon1, BackIcon} from './LearningbElements'
import useFetch from '../useFetch'
import ReactPlayer from 'react-player'

const LearningB = () => {
  const apiUrl= "http://localhost:1337"
    const {loading, error,data} = useFetch(
      `${apiUrl}/api/simulasis?populate=*`
    )
    if (loading) return <p>Loading...</p>
    if (error) return <p>Error...</p>
  
  return ( 
    <LearningContainer id="learningb">
        <FormIcon1 to='/'>
            <BackIcon />
        </FormIcon1>
        <LearningH1> Kelas Bencana </LearningH1>
        <LearningWrapper>
            {data.data.map((simulasi) => (
              <LearningCard key={simulasi.id}>
                <ReactPlayer
                 url={`${apiUrl}${simulasi.attributes.picture.data.attributes.url}`} light={`${apiUrl}${simulasi.attributes.gambar.data.attributes.formats.thumbnail.url}`} controls width="480px" height="270px" alt={simulasi.attributes.title} style={{margin:20, aspectRatio:"auto"}} className="favImage" />
                <LearningH2>Kelas {simulasi.attributes.title} </LearningH2>
                <LearningP> {simulasi.attributes.description} </LearningP>
            </LearningCard>))}            
        </LearningWrapper>
    </LearningContainer>
  )
}

export default LearningB