import React from 'react'
import { ShopContainer, ShopH1, ShopWrapper, ShopCard, ShopH2, ShopP, ShopIcon, FormIcon1, BackIcon} from './ShopbElements'
import useFetch from '../useFetch'
import { Button } from '../ButtonElements'

const ShopB = () => {
  const apiUrl= "http://localhost:1337"
    const {loading, error,data} = useFetch(
      `${apiUrl}/api/starterpacks?populate=*`
    )
    if (loading) return <p>Loading...</p>
    if (error) return <p>Error...</p>
  
  return ( 
    <ShopContainer id="shopb">
        <FormIcon1 to='/'>
            <BackIcon />
        </FormIcon1>
        <ShopH1> Starterpacks Bencana </ShopH1>
        <ShopWrapper>
            {data.data.map((starterpacks) => (
              <ShopCard key={starterpacks.id}>
                <ShopIcon Image src={`${apiUrl}${starterpacks.attributes.picture.data.attributes.url}`} alt={starterpacks.attributes.title} className="favImage" />
                <ShopH2> {starterpacks.attributes.title} </ShopH2>
                <ShopP> {starterpacks.attributes.description} </ShopP>
            </ShopCard>))}            
        </ShopWrapper>
    </ShopContainer>
  )
}

export default ShopB