import React from 'react'
import { Container, FormWrap, Icon, FormContent, Form, FormH1, FormLabel, FormInput, FormButton, BackIcon, NavBtnLink } from './ForgetPwElements'

const ForgetPw = () => {
  return (
    <>
        <Container id='forgetpw'>
            <FormWrap>
                <Icon to="/">ISTERN</Icon>
                <FormContent>
                    <Form action="#">
                        <NavBtnLink to='/signin'>
                            <BackIcon />
                        </NavBtnLink>
                        <FormH1>Bring back your account</FormH1>
                        <FormLabel htmlfor='for'>Email</FormLabel>
                        <FormInput type='email' required />
                        <FormLabel htmlfor='for'>Verification Code</FormLabel>
                        <FormInput type='password' required />
                        <FormLabel htmlfor='for'>New Password</FormLabel>
                        <FormInput type='password' required />
                        <FormButton type='submit'>Get your account</FormButton>
                    </Form>
                </FormContent> 
            </FormWrap>
        </Container>
    </>
  )
}

export default ForgetPw