import React from 'react'
import { InfoContainer, InfoWrapper, InfoRow, Column1, Column2, TextWrapper, TopLine, Heading, Subtitle, BtnWrap, ImgWrap, Img, NavBtnLink } from './LearningElements'

const LearningSection = ({ bests, lightBg, id, imgStart, topLine, lightText, headline, darkText, description, buttonLabel, img, alt, primary, dark, dark2}) => {
  
  console.log(bests);
  return (
    <div>
        <InfoContainer lightBg={lightBg} id={id}>
            <InfoWrapper>
                <InfoRow imgStart={imgStart}>
                        <Column1>
                        <TextWrapper>
                            <TopLine lightText={lightText}>{topLine}</TopLine>
                            <Heading lightText={lightText} >{headline}</Heading>
                            <Subtitle darkText={darkText}>{description}</Subtitle>
                            <BtnWrap>
                                <NavBtnLink to='/learningb' 
                                smooth={true}
                                duration={500}
                                spy={true}
                                exact="true"
                                offset={-80} 
                                primary={primary ? 1 : 0}
                                dark={dark ? 1 : 0}                             
                                dark2={dark2 ? 1 : 0}                             
                                > {buttonLabel} </NavBtnLink>
                            </BtnWrap>
                        </TextWrapper>
                    </Column1>
                    <Column2>
                        <ImgWrap>
                            <Img src={img} alt={alt}/>
                        </ImgWrap>
                    </Column2>
                </InfoRow>
            </InfoWrapper>
        </InfoContainer>
    </div>
  )
}

export default LearningSection